import React, { Component } from "react";
import {Grid} from '@material-ui/core';
import "./style.scss";




export default class TimelineCurso extends Component{

    render(){

        return(
            <Grid container className="timeline-curso-wrapper" xs={12}  md={12}>

                <Grid item className="timeline-curso-header" xs={12} md={12}>

                <span className="timeline-page-title"> O que voc vai aprender?</span>
                <span className="timeline-page-subtitle"> As videoaulas do curso online esto divididas em 4 partes, sendo elas:</span>
                </Grid>

                <Grid item className="timeline-content-wrapper" xs={12} md={12}>

                <ul className="timeline">
                    <li>
                        <div className="direction-r">
                            <div className="flag-wrapper">
                                <span
                                    className="flag">Parte 1 - O funcionamento da fertilidade feminina e masculina</span>

                            </div>
                            <div className="desc">
                                <span className="text-desc"> Aqui voc vai aprender um pouco da histria da percepo de fertilidade; como funciona a fertilidade masculina; anatomia genital interna feminina; funcionamento do ciclo menstrual; muco cervical e fertilizao</span>
                            </div>
                        </div>
                    </li>

                    <li>
                        <div className="direction-l">
                            <div className="flag-wrapper">
                                <span className="flag">Parte 2 - Como usar o mtodo</span>

                            </div>
                            <div className="desc">
                                <span className="text-desc">Nesse tpico explicaremos o uso do grfico; como fazer a observao e interpretao do muco, temperatura basal, colo do tero, alm de outras observaes importantes</span>
                            </div>
                        </div>
                    </li>

                    <li>
                        <div className="direction-r">
                            <div className="flag-wrapper">
                                <span className="flag">Parte 3 - O mtodo no seu cotidiano</span>
                            </div>
                            <div className="desc">
                                <span className="text-desc">Estudaremos como funciona o mtodo sintotermal na vida real; como diferenciar muco de outros fluidos; uso do mtodo para contracepo e concepo</span>
                            </div>
                        </div>
                    </li>


                    <li>
                        <div className="direction-l">
                            <div className="flag-wrapper">
                                <span className="flag">Parte 4 - Aulas bnus</span>
                            </div>
                            <div className="desc">
                                <span className="text-desc">Tambm contaremos com aulas bnus de outras  profissionais incrveis, que vo agregar muito no seu conhecimento e empoderamento sobre sua sade sexual e reprodutiva</span>

                            </div>
                        </div>
                    </li>

                </ul>
                </Grid>

            </Grid>
        );
    }
}