import React, { Component } from "react";
import {Grid} from '@material-ui/core';
import "./style.scss";
import BenefitsImage from "../../../Assets/Curso/benefitsImage.png";
import {faAngleDown, faCircle} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";



export default class BenefitsCurso extends Component{

    render(){

        return(
            <Grid container className="benefits-curso-wrapper" sm={12} lg={12} md={12}>

                <Grid container className="benefits-curso-content" sm={12} lg={12} md={12}>

                    <Grid item className="benefits-image-wrapper" sm={12} lg={6}>
                        <img className="curso-benefits-image" src={BenefitsImage}/>
                    </Grid>

                    <Grid item className="benefits-text-wrapper" sm={12} lg={6}>

                        <Grid container className="text-wrapper" sm={12} lg={9}>
                        <span className="benefits-curso-title"> Quais so as vantagens de usar percepo de fertilidade?</span>

                            <span className="benefits-curso-text"><FontAwesomeIcon className=" icon-li benefits-curso-icon" icon={faCircle} size="xs" color="#404040"/>Eficcia contraceptiva de 99,6%</span>

                            <span className="benefits-curso-text"><FontAwesomeIcon className="icon-li benefits-curso-icon" icon={faCircle} size="xs" color="#404040"/>Pode ser usado para facilitar a concepo e identificar
                                possveis problemas de fertilidade</span>

                            <span className="benefits-curso-text"><FontAwesomeIcon className="icon-li benefits-curso-icon" icon={faCircle} size="xs" color="#404040"/>Perspectiva feminista: qualquer menstruante pode usar,
                                independente de sua identidade de gnero,orientao sexual, quantidade de parceires e
                                inteno reprodutiva</span>

                            <span className="benefits-curso-text"><FontAwesomeIcon className="icon-li benefits-curso-icon" icon={faCircle} size="xs" color="#404040"/>Entende a percepo menstrual como uma forma de ganhar
                                fluncia corporal</span>

                            <span className="benefits-curso-text"><FontAwesomeIcon className="icon-li benefits-curso-icon" icon={faCircle} size="xs" color="#404040"/>Entende o ciclo menstrual como um sinal vital</span>

                            <span className="benefits-curso-text"><FontAwesomeIcon className="icon-li benefits-curso-icon" icon={faCircle} size="xs" color="#404040"/>Tem uma viso holstica do ser humano e de seus
                                processos de sade e doena</span>


                        </Grid>



                    </Grid>

                </Grid>


            </Grid>
        );
    }
}