import React, { Component } from "react";
import {Grid} from '@material-ui/core';
import "./style.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faChevronDown} from "@fortawesome/free-solid-svg-icons";




export default class InfoAtendimentoIndividual extends Component{

    render(){

        return(
            <Grid container className="info-atendimento-individual" xs={12} md={12} lg={12}>

                            <FontAwesomeIcon className="faq-icon-primary" icon={faChevronDown} size="2x" color="#471E67"/>

                            <span className="titulo-secundario">O primeiro e mais completo curso de percepo de fertilidade do Brasil</span>



                            <Grid container className="atendimento-informacoes-content" xs={12} sm={12} lg={9} md={12}>

                                <Grid item className="info-card" xs={9} sm={8} md={3}>

                                    <div className="card-image-1">

                                        <div className="ribbon3">
                                            <span className="ribbon-title">Informaes gerais</span>
                                            <span className="ribbon-subtitle">Objetivo e metodologia</span>
                                        </div>

                                    </div>

                                    <div className="info-card-conteudo">

                                    <span className="info-card-title"> Aprofunde-se no mtodo Justisse</span>
                                    <span className="info-card-text"> Aprenda e aplique o mtodo de percepo de fertilidade
                                        Justisse com atendimento individual</span>

                                    <span className="info-card-title"> Curso online + sesses remotas</span>
                                    <span className="info-card-text"> Voc recebe acesso a um curso 100% online e tambm
                                        ter direito a 5 sesses de acompanhamento individual de 1 hora cada (no Zoom)</span>
                                    </div>

                                </Grid>

                                <Grid item className="info-card" xs={9} sm={8} md={3}>

                                    <div className="card-image-2">
                                        <div className="ribbon3">
                                            <span className="ribbon-title">Para quem  o curso?</span>
                                            <span className="ribbon-subtitle">Pblico e pr-requisito</span>
                                        </div>
                                    </div>
                                    <div className="info-card-conteudo">
                                    <span className="info-card-title"> Pblico-alvo</span>
                                    <span className="info-card-text">Para quem quer a mxima eficcia do mtodo e um
                                        atendimento personalizado e intimista</span>

                                    <span className="info-card-title"> No existe pr-requisito!</span>
                                    <span className="info-card-text"> Voc recebe um material de apoio completo para aprender
                                        o mtodo desde o bsico e suporte personalizado para tirar dvidas e atingir o uso
                                        perfeito</span>
                                    </div>
                                </Grid>

                                <Grid item className="info-card" xs={9} sm={8} md={3}>
                                    <div className="card-image-3">
                                        <div className="ribbon3">
                                            <span className="ribbon-title">Carol Fischmann</span>
                                            <span className="ribbon-subtitle">Sobre a instrutora</span>
                                        </div>
                                    </div>

                                    <div className="info-card-conteudo">
                                    <span className="info-card-text"> <b>Carol Fischmann</b>  antroploga e tem formao como Instrutora
                                de Sade Reprodutiva Holstica no Justisse College, um curso de dois anos para formar instrutoras
                                de percepo de fertilidade. Fundadora do projeto Fluncia Corporal, Carol atua como instrutura
                                desde 2017 e j ajudou dezenas de menstruantes.</span>
                                    </div>
                                </Grid>

                            </Grid>


                        </Grid>
            
        );
    }
}