import React, { Component } from "react";
import {Divider, ExpansionPanel, ExpansionPanelDetails, ExpansionPanelSummary, Grid} from '@material-ui/core';
import "./style.scss";
import Footer from "../../Components/Footer";
import SimpleMenu from "../../Components/Menu";
import HeaderAtendimentoIndividual from "../../Components/AtendimentoIndividual/HeaderAtendimentoIndividual";
import InfoAtendimentoIndividual from "../../Components/AtendimentoIndividual/InfoAtendimentoIndividual";
import BenefitsCurso from "../../Components/Curso/BenefitsCurso";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faAngleDown, faPlus} from "@fortawesome/free-solid-svg-icons";
import FeaturesIndividual from "../../Components/AtendimentoIndividual/FeaturesAtendimento";
import Depoimentos from "../../Components/AtendimentoIndividual/Depoimento";
import {Link} from 'react-router-dom'

const produto = {
    title: "Atendimento individual",
    price: "1499"
}

export default class AtendimentoIndividual extends Component{

    render(){

        return(
            <Grid container className="atendimento-individual-container" xs={12} lg={12} md={12}>
                <SimpleMenu/>

                <HeaderAtendimentoIndividual/>


                <Grid container className="atendimento-individual-conteudo" xs={12} sm={12} lg={12} md={12}>

                    <InfoAtendimentoIndividual/>

                    <BenefitsCurso/>


                {/*    /!*TIMELINE *!/*/}



                    <Grid container className="atendimento-timeline-container" xs={12}  md={12}>

                        <Grid item className="atendimento-timeline-header" xs={12} md={12}>

                            <span className="titulo-primario"> Como vai funcionar?</span>

                        </Grid>

                        <Grid item className="timeline-content-wrapper" xs={12} md={12}>

                            <ul className="timeline">
                                <li>
                                    <div className="direction-r">
                                        <div className="flag-wrapper">
                                <span
                                    className="flag">Assista s videoaulas sobre o funcionamento da fertilidade e uso
                                    do mtodo</span>

                                        </div>
                                        <div className="desc">
                                            <span className="text-desc"> Aqui voc vai receber uma srie de videos e
                                                materiais complementares para estudar sozinha antes dos atendimentos comearem</span>
                                        </div>
                                    </div>
                                </li>

                                <li>
                                    <div className="direction-l">
                                        <div className="flag-wrapper">
                                            <span className="flag">Comece a praticar!</span>

                                        </div>
                                        <div className="desc">
                                            <span className="text-desc">Assim que voc terminar de assistir as
                                                videoaulas, j comece a fazer seus grficos.  aqui que vo surgir as
                                                principais dvidas</span>
                                        </div>
                                    </div>
                                </li>

                                <li>
                                    <div className="direction-r">
                                        <div className="flag-wrapper">
                                            <span className="flag">Participe dos atendimentos individuais</span>
                                        </div>
                                        <div className="desc">
                                            <span className="text-desc">Ao longo de 5 sesses de acompanhamento de 1
                                                hora cada, voc ter a oportunidade de tirar todas as suas dvidas e
                                                aprender com um atendimento individualizado e intimista</span>
                                        </div>
                                    </div>
                                </li>


                                <li>
                                    <div className="direction-l">
                                        <div className="flag-wrapper">
                                            <span className="flag">Continue se aprofundando</span>
                                        </div>
                                        <div className="desc">
                                            <span className="text-desc">Voc ir receber tambm um conjunto de videos e
                                                materiais complementares para estudar sozinha durante e depois de
                                                terminar os atendimentos</span>

                                        </div>
                                    </div>
                                </li>

                            </ul>
                        </Grid>

                    </Grid>


                {/*    /!*INFORMAES SOBRE O ATENDIMENTO*!/*/}

                    <Grid container className="atendimento-sessoes-container" xs={12} lg={12} md={12}>

                    <Grid container className="atendimento-sessoes" xs={12} lg={6} md={8}>

                        <span className="titulo-primario">Como vo funcionar as sesses de atendimento?</span>

                    <span className="texto-primario"style={{textAlign: 'center'}}>Nas sesses de atendimento partiremos
                        do seu objetivo especfico pelo qual procurou o mtodo e adaptaremos as sesses e contedo
                        conforme sua necessidade. Analisaremos o que est acontecendo no seu grfico e tiraremos todas
                        as as dvidas que voc tiver durante as observaes, garantindo assim a mxima eficcia do
                        mtodo de percepo de fertilidade.</span>

                    </Grid>

                    </Grid>


                <Depoimentos/>
                <FeaturesIndividual/>


                {/*/!*PREO DO ATENDIMENTO*!/*/}


                    <Grid container className="value-curso-wrapper" xs={12} lg={12} md={12}>


                        <Grid container className="value-curso-card" xs={12} md={5}>


                            <Grid item className="value-curso-card-header" xs={12} md={12}>

                                <h2 className="value-card-title"> {produto.title}</h2>
                                <h1 className="value-card-price"> R$ {produto.price}</h1>
                                <span className="value-card-secondary-text"> No carto de crdito ou boleto</span>
                            </Grid>

                            <Grid item className="value-curso-card-body" xs={12} md={12}>
                                <Divider/>
                                <span className="value-card-text">Material de percepo de fertilidade mais completo do Brasil</span>
                                <Divider/>
                                <span className="value-card-text"> Instrutora certificada</span>
                                <Divider/>
                                <span className="value-card-text"> 5 sesses de atendimento individual</span>
                                <Divider/>

                                <Link to="/checkout" className="value-card-link" >Comprar</Link>

                                <span className="value-card-secondary-text" style={{fontSize: '1rem', marginTop: '16px'}}> Vagas limitadas</span>
                            </Grid>
                        </Grid>

                    </Grid>

                <Grid container className="contato-atendimento" xs={12} md={12}>

                    <h1 className="titulo-primario ">Ainda no sabe se o atendimento individual  a melhor opo para
                        voc?</h1>

                    <span className="texto-primario" style={{ textAlign: 'center'}}>Escreva pra gente e conta mais sobre voc e suas dvidas e
                        responderemos assim que possvel! </span>

                    <Grid container className="contato-conteudo" xs={12} md={6}>

                    <input className="formulario-contato" id="nome" name="nome" placeholder="Nome*"
                           type="text"/>

                    <input className="formulario-contato" id="email" name="email" placeholder="Email*"
                           type="email"/>

                    <textarea id="mensagem" name="mensagem" className="formulario-contato-texto" rows="4" cols="50" placeholder="Mensagem">
                        </textarea>

                    <button className="formulario-botao" type="submit">Enviar</button>
                    </Grid>

                </Grid>



                {/*    /!*PROGRAMAS *!/*/}


                    <Grid container className="programs-wrapper" xs={12} lg={12} md={12}>

                        <Grid container className="programs-content" xs={12} lg={12} md={12}>

                            <span className="programs-title"> Conhea nossos outros programas</span>

                            <Grid container className="programs-card-wrapper" xs={12} lg={12} md={12}>

                                <Grid item className="programs-card" xs={12} md={4}>
                                    <span className="programs-card-title"> Curso Online</span>
                                    <span className="programs-card-text">Para quem tem interesse no mtodo e quer um
                                        empurrozinho para comear a estudar</span>
                                    <button className="card-button">Quero conhecer</button>
                                </Grid>


                                <Grid item className="programs-card" xs={12} md={4}>
                                    <span className="programs-card-title"> Atendimento em grupo</span>
                                    <span className="programs-card-text">Para quem quer a mxima eficcia do mtodo e
                                        gosta de aprender trocando com outras pessoas</span>
                                    <button className="card-button">Quero conhecer</button>
                                </Grid>



                            </Grid>

                        </Grid>


                    </Grid>




                {/*    /!*FAQ CURSO INDIVIDUAL*!/*/}


                    <Grid container className="faq-wrapper" xs={12} md={12}>

                        <Grid container className="faq-content" xs={12} md={8}>

                            <div className="expansion-panel-faq">
                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                        aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Eu j pratico percepo de fertilidade h
                                            algum tempo. Vale a pena fazer o atendimento individual?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text">
                                            Sim! Se voc j estudou outro mtodo ou tem uma prtica intuitiva da sua
                                            fertilidade, voc provavelmente ter mais facilidade de aprender o Justisse,
                                            mas como o mtodo  cheio de sutilezas, tenho certeza que voc aprender
                                            muito e conseguir conquistar o to sonhado uso perfeito!
                                        </span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Eu posso fazer o atendimento em grupo se
                                            uso contracepo hormonal (plula, DIU hormonal, adesivo, anel, implante)?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text">
                                            Depende. Os anticoncepcionais hormonais interferem no ciclo menstrual
                                            (a maioria deixa de ovular), impossibilitando o uso do mtodo. Agora se voc
                                            est pensando em parar de usar anticoncepcional, o suporte que voc receber
                                            ao longo do atendimento individual ser muito valioso! Parar a contracepo
                                            hormonal pode ser bem desafiadora para algumas pessoas, ento praticar
                                            percepo de fertilidade durante essa fase  um enorme privilgio, pois
                                            voc ir entender melhor o que est acontecendo no seu corpo, ter um mtodo
                                            contraceptivo ou conceptivo e tambm ter o suporte para encontrar medidas
                                            de autocuidado para passar por essa transio de forma mais leve.
                                        </span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Eu posso fazer o atendimento individual se uso DIU de cobre? </span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text"> Sim! O DIU no afeta o ciclo menstrual,
                                            ento voc pode usar o mtodo sem nenhum problema!</span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Eu vou poder parar de usar camisinha se usar percepo de fertilidade?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text">
                                            O mtodo de percepo de fertilidade tem uma eficcia contraceptiva de
                                            99,6%, portanto, voc no precisar usar nenhum tipo de barreira durante o
                                            seu perodo infrtil a partir do momento em que voc souber usar direito o
                                            mtodo de percepo de fertilidade. Durante o perodo frtil (cerca de 6 a
                                            10 dias em ciclos saudveis),  necessrio praticar abstinncia ou usar
                                            barreiras. Porm  sempre importante lembrar que o preservativo  a nica
                                            forma de se prevenir contra ISTs, ento recomendamos que voc continue
                                            usando camisinha sempre. </span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Casal pode fazer o atendimento junto?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text">Sim!!! E, particularmente, eu acho incrvel
                                            quando ambos esto envolvidos na prtica do mtodo e compartilham a
                                            responsabilidade de atingir o mesmo objetivo reprodutivo (seja concepo
                                            ou contracepo).</span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Qual a diferena entre o curso online e
                                            o atendimento individual?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text"> O curso online  voltado para pessoas que
                                            querem estudar sozinhas e aprender o mtodo no seu prprio tempo. No
                                            atendimento individual, alm de receber todos os materiais do curso online,
                                            voc ter 5 sesses de acompanhamento de 1 hora cada totalmente
                                            personalizadas para as suas necessidades. Nestes encontros, compartilhamos
                                            dvidas, insights, dificuldades e alegrias e eu reviso seu grfico para
                                            avaliar se voc est fazendo um uso correto.  graas a este contato
                                            contnuo que eu consigo garantir que voc no est fazendo nenhum erro
                                            que poderia comprometer a eficcia do mtodo.</span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Depois de terminar o atendimento
                                            individual, eu vou estar apta a fazer o uso perfeito do mtodo?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text"> Para a maioria das pessoas, as 5 sesses
                                            de acompanhamento so suficientes para entender adequadamente como usar o
                                            mtodo. No entanto, isso depender da sua dedicao  aprendizagem e tambm
                                            da complexidade dos seus ciclos. Em alguns casos pode ser recomendvel
                                            adquirir algumas sesses de acompanhamento extras.</span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header"> Depois de terminar o atendimento
                                            individual, eu posso adquirir mais sesses individuais?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text"> Sim! Se voc sentir necessidade de mais
                                            acompanhamento, voc pode comprar sesses avulsas por x. Algumas pessoas
                                            simplesmente precisam de mais tempo e ateno para aprender a usar o mtodo.
                                            Depois de alguns meses ou anos, voc pode sentir a necessidade de uma nova
                                            sesso, seja para garantir que voc continua no caminho, ou porque voc
                                            passou por uma grande mudana e necessita mais suporte (por exemplo: estava
                                            usando o mtodo para contracepo e agora quer usar para concepo ou
                                            vice-versa; est amamentando e precisa de auxlio nessa nova fase; est
                                            entrando na perimenopausa e deseja mais suporte; est notando mudanas no
                                            seu ciclo e precisa de apoio para entend-la; etc.).</span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>

                                <ExpansionPanel>
                                    <ExpansionPanelSummary expandIcon={<FontAwesomeIcon className="faq-icon-primary" icon={faAngleDown} size="1.5x" color="#CA4B49"/>}
                                                           aria-controls="panel1a-content" id="panel1a-header">
                                        <span className="faq-expansion-header">Eu trabalho com mulheres, o atendimento
                                            individual vai me ajudar na minha prtica profissional?</span>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>

                                        <span className="faq-expansion-text"> Sim, porm este no  um curso
                                            profissionalizante. Isso significa que eu tenhocerteza que o contedo que
                                            voc vai receber neste curso e as discusses ao longo dos nossos encontros
                                            iro te ajudar a entender algumas coisas que voc talvez no soubesse e
                                            tambm iro te preparar para divulgar o mtodo de percepo de fertilidade
                                            de forma correta. Porm, a formao para se tornar uma instrutora  bem mais
                                            longa e intensa, ento voc no estar apta a ensinar o mtodo a outras
                                            pessoas. O atendimento individual foi pensado para uso pessoal e, por isso,
                                            no h certificado de concluso.</span>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>










                            </div>
                        </Grid>
                    </Grid>




                </Grid>

                <Footer/>
            </Grid>
        );
    }
}